<template>
  <div class="container">
    <div class="item">
      <div class="title">角色列表</div>
      <div class="role">
        <el-radio-group v-model="role">
          <el-radio :label="1">现场</el-radio>
          <el-radio :label="2">处级</el-radio>
          <el-radio :label="3">局级</el-radio>
        </el-radio-group>
      </div>
    </div>
    <div class="item">
      <div class="title">权限列表</div>
      <div class="role">
        <div class="info">基础数据</div>
        <el-checkbox-group v-model="base">
          <el-checkbox :label="1">井基础数据</el-checkbox>
          <el-checkbox :label="2">岩性数据</el-checkbox>
          <el-checkbox :label="3">地址分层及岩性描述</el-checkbox>
          <el-checkbox :label="4">钻井设备</el-checkbox>
          <el-checkbox :label="5">井身结构</el-checkbox>
          <el-checkbox :label="6">井口装置</el-checkbox>
        </el-checkbox-group>
      </div>
      <div class="role">
        <div class="info">轨迹数据</div>
        <el-checkbox-group v-model="profile">
          <el-checkbox :label="1">轨迹剖面</el-checkbox>
          <el-checkbox :label="2">轨迹数据</el-checkbox>
          <el-checkbox :label="3">轨迹</el-checkbox>
          <el-checkbox :label="4">轨迹图三维展示</el-checkbox>
        </el-checkbox-group>
      </div>
      <div class="role">
        <div class="info">生产数据</div>
        <el-checkbox-group v-model="develop">
          <el-checkbox :label="1">钻头统计</el-checkbox>
          <el-checkbox :label="2">动力钻具</el-checkbox>
          <el-checkbox :label="3">扶正器统计</el-checkbox>
          <el-checkbox :label="4">钻具组合</el-checkbox>
        </el-checkbox-group>
      </div>
    </div>
    <div class="submit">提交</div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      role: 1,
      base: [1],
      profile: [1],
      develop: [1]
    }
  },

  components: {},

  computed: {},

  mounted: function() {},

  methods: {}
}
</script>

<style lang='scss' scoped>
.container {
  margin: 20px 50px;
  .item {
    margin-bottom: 20px;
    .title {
      margin-bottom: 20px;
      padding-bottom: 8px;
      position: relative;
      &::before {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 66px;
        height: 2px;
        background: #333;
      }
    }
    .role {
      margin: 20px 50px;
      padding-bottom: 20px;
      border-bottom: 2px solid #eee;
      .info {
        margin-bottom: 20px;
      }
    }
  }
  .submit {
    margin: 20px auto;
    width: 200px;
    text-align: center;
    color: #fff;
    line-height: 40px;
    border-radius: 4px;
    cursor: pointer;
    background: var(--active);
  }
}
</style>



